@import "../../../styles/variables";
.l-login-history {

  &__inner {
    flex: 1 1 auto;
    flex-direction: column;
    align-items: normal;
    justify-content: flex-start;
    max-width: map-get( $layout-width, short );
  }//&__inner

  &__header-desktop {
    display: none;
  }//&__header-desktop

  &__panel {
    flex: 1 1 1px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    background: #FFFFFF;

    &__body {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      overflow: auto;

      > * {
        flex: none;
      }//> *

    }//&__body

  }//&__panel

}//.l-login-history

@import "LoginHistory-md-min";
